<template>
  <div>
    <!-- 面包屑导航区域 -->
    <bread-crumb>
      <span slot="manage">权限管理</span>
      <span slot="list">角色列表</span>
    </bread-crumb>

    <!-- 卡片视图 -->
    <el-card>
      <!-- 添加角色按钮区域 -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="showAddRoleDialog">添加角色</el-button>
        </el-col>
      </el-row>
      <!-- 添加角色对话框 -->
      <el-dialog
        title="添加角色"
        :visible.sync="addRoleDialogVisible"
        width="50%"
        :close-on-click-modal="false"
        @close="resetAddRoleForm"
      >
        <!-- 对话框主体区域 -->
        <!-- 添加角色表单区域 -->
        <el-form
          ref="addRoleFormRef"
          :model="addRoleForm"
          label-width="100px"
          :rules="addRoleFormRul"
        >
          <!-- 角色名称 -->
          <el-form-item label="角色名称" prop="roleName">
            <el-input v-model="addRoleForm.roleName"></el-input>
          </el-form-item>
          <!-- 角色描述 -->
          <el-form-item label="角色描述" prop="roleDesc">
            <el-input v-model="addRoleForm.roleDesc"></el-input>
          </el-form-item>
        </el-form>
        <!-- 对话框底部区域 -->
        <span slot="footer" class="dialog-footer">
          <el-button type="info" @click="resetFormBtn">重 置</el-button>
          <el-button type="primary" @click="addRoleBtn">确 定</el-button>
        </span>
      </el-dialog>

      <!-- 角色列表区域 -->
      <el-table :data="rolelist" border stripe>
        <!-- 展开列 -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row
              :class="['bdbottom', i1 === 0 ? 'bdtop' : '', 'vcenter']"
              v-for="(item1, i1) in scope.row.children"
              :key="item1.id"
            >
              <!-- 渲染一级权限 -->
              <el-col :span="5">
                <el-tag
                  closable
                  @close="removeRightById(scope.row, item1.id)"
                  >{{ item1.authName }}</el-tag
                >
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!-- 渲染二级和三级权限 -->
              <el-col :span="19">
                <!-- 通过for循环 嵌套渲染二级权限  -->
                <el-row
                  :class="[i2 === 0 ? '' : 'bdtop', 'vcenter']"
                  v-for="(item2, i2) in item1.children"
                  :key="item2.id"
                >
                  <el-col :span="6">
                    <el-tag
                      type="success"
                      closable
                      @close="removeRightById(scope.row, item2.id)"
                      >{{ item2.authName }}</el-tag
                    >
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <!-- 渲染三级权限 -->
                    <el-tag
                      type="warning"
                      v-for="item3 in item2.children"
                      :key="item3.id"
                      closable
                      @close="removeRightById(scope.row, item3.id)"
                      >{{ item3.authName }}</el-tag
                    >
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <!-- 索引列 -->
        <el-table-column type="index"></el-table-column>
        <el-table-column label="角色名称" prop="roleName"></el-table-column>
        <el-table-column label="角色描述" prop="roleDesc"></el-table-column>
        <el-table-column label="操作" width="300px">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditRoleDialog(scope.row.id)"
              >编辑</el-button
            >
            <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteRole(scope.row.id)"
              >删除</el-button
            >
            <el-button
              type="warning"
              icon="el-icon-setting"
              size="mini"
              @click="showSetRightDialog(scope.row)"
              >分配权限</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!-- 分配权限的对话框 -->
    <el-dialog
      title="分配权限"
      :visible.sync="setRightDialogVisible"
      width="50%"
      @close="setRightDialogClosed"
    >
      <!-- 树形控件 -->
      <el-tree
        :data="rightslist"
        :props="treeProps"
        show-checkbox
        node-key="id"
        default-expand-all
        :default-checked-keys="defKeys"
        ref="treeRef"
      ></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRightDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="allotRights">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 编辑角色对话框区域 -->
    <el-dialog
      title="编辑角色"
      :visible.sync="editRoleDialogVisible"
      width="50%"
      :close-on-click-modal="false"
      @close="resetEditRoleForm"
    >
      <!-- 对话框主体区域 -->
      <!-- 添加角色表单区域 -->
      <el-form
        ref="editRoleFormRef"
        :model="editRoleForm"
        label-width="100px"
        :rules="addRoleFormRul"
      >
        <!-- 角色名称 -->
        <el-form-item label="角色名称" prop="roleName">
          <el-input v-model="editRoleForm.roleName"></el-input>
        </el-form-item>
        <!-- 角色描述 -->
        <el-form-item label="角色描述" prop="roleDesc">
          <el-input v-model="editRoleForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <!-- 对话框底部区域 -->
      <span slot="footer" class="dialog-footer">
        <el-button plain @click="editRoleDialogVisible=false">取 消</el-button>
        <el-button type="primary" @click="editRoleBtn">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import BreadCrumb from "../common/BreadCrumb";
export default {
  components: { BreadCrumb },
  data() {
    return {
      // 所有角色列表数据
      rolelist: [],
      // 控制添加角色对话框显示和隐藏
      addRoleDialogVisible: false,
      // 添加角色表单对象
      addRoleForm: {
        roleName: '',
        roleDesc: ''
      },
      // 添加角色表单验证规则
      addRoleFormRul: {
        // 角色名称验证
        roleName: [
          { required: true, message: '请输入角色名称', trigger: 'blur' },
          {
            min: 2,
            max: 6,
            message: '角色名称需要 2 到 6 个字符组成',
            trigger: 'blur'
          }
        ],
        // 角色描述规则
        roleDesc: [
          { required: true, message: '请输入角色描述', trigger: 'blur' }
        ]
      },
      //控制编辑角色对话框显示和隐藏
      editRoleDialogVisible: false,
      //编辑角色表单
      editRoleForm: {},
      // 控制分配权对话框的显示与隐藏
      setRightDialogVisible: false,
      // 所有权限的数据
      rightslist: [],
      // 属性控件的属性绑定对象
      treeProps: {
        label: "authName",
        children: "children",
      },
      // 默认选中的节点Id值数组
      defKeys: [],
      // 当前即将分配权限角色id
      roleId: "",
    };
  },
  created() {
    this.getRolesList();
  },
  methods: {
    // 获取所有用户列表
    async getRolesList() {
      const { data: res } = await this.$http.get("roles");
      if (res.meta.status !== 200) {
        return this.$message.error("获取角色列表失败！");
      }
      this.rolelist = res.data;
      console.log(this.rolelist);
    },
    // 点击显示添加角色对话框
    showAddRoleDialog() {
      this.addRoleDialogVisible = true;
    },
    // 重置添加角色表单
    resetAddRoleForm() {
      this.$refs.addRoleFormRef.resetFields();
    },
    // 重置按钮点击事件
    resetFormBtn() {
      this.resetAddRoleForm();
    },
    // 提交添加角色请求
    addRoleBtn() {
      // 表单提交预验证
      this.$refs.addRoleFormRef.validate(async valid => {
        if (!valid) return null;
        // 预验证通过，发起请求
        const { data: res } = await this.$http.post('roles', this.addRoleForm);
        if (res.meta.status !== 201) {
          return this.$message.error(res.meta.status + ' : 添加角色失败！');
        }
        // 请求添加角色成功
        // 重新读取角色列表
        this.getRolesList();
        // 关闭对话框
        this.addRoleDialogVisible = false;
        //   返回成功提示
        return this.$message.success(res.meta.msg);
      });
    },
    // 删除角色
    async deleteRole(id) {
      // console.log(id);
      // 弹框提示用户是否确认删除角色
      const delConfirm = await this.$confirm(
        '此操作将永久删除该角色, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch(err => err);

      if (delConfirm !== 'confirm') return null;
      //   确认删除，发起请求
      const { data: res } = await this.$http.delete('roles/' + id);
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.status + ' : 删除角色失败！');
      }
      //   删除成功
      // 重新读取角色列表
      this.getRolesList();
      //   返回成功提示
      return this.$message.success(res.meta.msg);
    },
    // 点击显示编辑角色对话框
    async showEditRoleDialog(id) {
      // 发起查询角色信息请求
      const { data: res } = await this.$http.get('roles/' + id);
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.status + ' : 获取角色信息失败');
      }
      this.editRoleForm = res.data;
      this.editRoleDialogVisible = true;
    },
    // 重置编辑角色表单
    resetEditRoleForm() {
      this.$refs.editRoleFormRef.clearValidate();
    },
    // 角色编辑提交按钮事件
    editRoleBtn() {
      // 表单预验证
      this.$refs.editRoleFormRef.validate(async valid => {
        if (!valid) return null;
        // 请求修改角色
        const { data: res } = await this.$http.put(
          'roles/' + this.editRoleForm.roleId,
          {
            roleName: this.editRoleForm.roleName,
            roleDesc: this.editRoleForm.roleDesc
          }
        );
        if (res.meta.status !== 200) {
          return this.$message.error(res.meta.status + ' : 修改角色信息失败');
        }
        // 请求修改成功
        // 重新读取角色列表
        this.getRolesList();
        // 关闭对话框
        this.editRoleDialogVisible = false;
        // 返回成功提示
        return this.$message.success('角色信息修改成功！');
      });
    },
    // 根据Id删除对应的权限
    async removeRightById(role, rightId) {
      // 弹窗提示用户是否要删除
      const confirmResult = await this.$confirm(
        "此操作将永久删除该文件，是否继续？",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }
      ).catch((err) => err);
      if (confirmResult !== "confirm") {
        return this.$message.info("取消了删除！");
      }
      const { data: res } = await this.$http.delete(
        `roles/${role.id}/rights/${rightId}`
      );
      if (res.meta.status !== 200) {
        return this.$message.error("删除权限失败！");
      }
      role.children = res.data;
    },
    // 展示分配权限的对话框
    async showSetRightDialog(role) {
      this.roleId = role.id;
      // 获取所有权限的数据
      const { data: res } = await this.$http.get("rights/tree");
      if (res.meta.status !== 200) {
        return this.$message.error("获取权限数据失败！");
      }
      // 把获取到的权限数据保存到data中
      this.rightslist = res.data;
      console.log(this.rightslist);
      // 递归获取三级节点的Id
      this.getLeadKeys(role, this.defKeys);
      this.setRightDialogVisible = true;
    },
    // 通过递归的形式，获取角色下所有的三级权限的id，并保存到deKeys数组中
    getLeadKeys(node, arr) {
      // 如果当前node节点不包含children属性，则是三级节点
      if (!node.children) {
        return arr.push(node.id);
      }
      node.children.forEach((item) => this.getLeadKeys(item, arr));
    },
    // 监听分配权限对话框的关闭事件
    setRightDialogClosed() {
      this.defKeys = [];
    },
    // 点击为角色分配权限
    async allotRights() {
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys(),
      ];
      const idStr = keys.join(",");
      const { data: res } = await this.$http.post(
        `roles/${this.roleId}/rights`,
        { rids: idStr }
      );
      if (res.meta.status !== 200) {
        return this.$message.error("分配权限失败！");
      }
      this.$message.success("分配权限成功！");
      this.getRolesList();
      this.setRightDialogVisible = false;
    },
  },
};
</script>

<style lang="less" scoped>
.el-tag {
  margin: 7px;
}
.bdtop {
  border-top: 1px solid #eee;
}
.bdbottom {
  border-bottom: 1px solid #eee;
}
</style>